import React from 'react'

const OpenSourceProjects = ( ) => {
    const opsProjects = [
        {
            img:'static/img/ops/gatsby-all-in.png',
            title:'gatsby-all-in',
            link:'https://github.com/Gherciu/gatsby-all-in',
            description:'Gatsby starter for creating any type of site. Preconfigured out of the box to work well with Eslint, Husky, Tailwind, Redux and much more. Read more about gatsby-all-in on official <a href="https://www.gatsbyjs.org/starters/Gherciu/gatsby-all-in/" target="_blank">Gatsby Starters Page</a>.',
            website:'https://gatsby-all-in.netlify.com/'
        },
        {
            img:'static/img/ops/commitlint-jira.png',
            title:'commitlint-jira',
            link:'https://github.com/Gherciu/commitlint-jira',
            description:'A monorepo that contains a plugin and a config for commitlint that enforce Jira commits messages style. You can find this packages and installation docs also on <a href="https://www.npmjs.com/package/commitlint-config-jira" target="_blank">NPM</a>'
        },
        {
            img:'static/img/ops/graphiql-storm.jpg',
            title:'graphiql-storm',
            link:'https://github.com/Gherciu/graphiql-storm',
            description:'A GraphQl web IDE. GraphiQL-Storm uses components of GraphiQL under the hood but is meant as a more powerful GraphQL IDE enabling better (local) development workflows.',
            website:'https://gherciu.github.io/graphiql-storm/'
        },
        {
            img:'static/img/ops/multipack.png',
            title:'multipack',
            link:'https://github.com/Gherciu/multipack',
            description:'A monorepo template repository. Preconfigured to work well with Lerna, Typescript, Jest, Webpack, Eslint'
        },
        {
            img:'static/img/ops/no-backend.png',
            title:'no-backend',
            link:'https://github.com/Gherciu/no-backend',
            description:'Transform your database into working GraphQl schema. Creates Querys, Mutations, Subscriptions and resolvers for all tables'
        }  
    ]
    return (
    <div className='content-wrapper'>
        <div className="ops-container">
        {opsProjects.map((item,index)=>
            <div className="ops-card-container" key={index}>
                <div className="ops-card" >
                    <div className="description">
                        <a href={item.link} target='__blank' className='ops-card-label'>
                            <img src='static/img/ops/github.png' />
                        </a>
                        {item.website &&
                            <a href={item.website} target='__blank' className='ops-card-label-website'>
                                <img src='static/img/ops/browser.png' />
                            </a>
                        }
                        <a href={item.link} target='__blank' className='ops-link'>
                            <h3>
                                {item.title}
                            </h3>
                        </a>
                        <span dangerouslySetInnerHTML={{__html: item.description}} />
                    </div>
                    <img src={item.img} className='ops-card-img' alt=""/>
                </div>
            </div>
        )}
            <a target="_blank" href="https://github.com/Gherciu?tab=repositories" className="button-link">
                <img className="github-icon" src="static/img/icons/github.svg" />
                See more on GitHub
            </a>
        </div>
        <style jsx>{`
            .github-icon {
                width: 25px;
                margin-right: 10px;
                vertical-align: middle;
            }
            .button-link {
                display: flex;
                align-items: center;
            }
            .ops-container{
                display: flex;
                align-items: center;
                flex-direction: column;
                margin-bottom: 150px;
            }
            .ops-card-container{
                width: 100%;
                display: flex;
                align-items: center;
                flex-direction: column;
            }
            .ops-card{
                max-width:900px;
                width:100%;
                min-height:230px;
                border-radius:24px;
                overflow: hidden;
                background: #fff;
                margin-bottom: 50px;
                box-sizing: border-box;
                display: flex;
                align-items: center;
                justify-content: space-between;
                box-shadow: 0 10px 25px rgba(0,0,0,0.12), 9px 10px 45px rgba(0,0,0,0.24);
                position: relative;
                border-bottom: 10px solid #eaeaea;
            }
            .ops-card-label{
                display:block;
                position: absolute;
                top:20px;
                left:20px;
            }
            .ops-card-label-website{
                display:block;
                position: absolute;
                top:20px;
                left:60px;
            }
            .ops-card-label-website>img{
                width:30px;
                height:30px;
            }
            .ops-card-label>img{
                width:30px;
                height:30px;
            }
            .description{
                padding:0px 15px;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }
            .ops-card-img{
                object-fit: cover;
                max-width:220px;
                height: 220px;
                box-sizing: border-box;
            }
            .ops-link{
                color: #333;
                text-decoration: none;
                font-size:18px;
                border-bottom: 3px solid #20AADB;
                display: block;
                padding: 0px 5px;
                line-height: 0px;
                height:35px;
                margin-bottom: 10px;
                box-sizing: border-box;
                transition: all 0.5s;
            }
            .ops-link:hover{
                border-bottom: 30px solid #20AADB;
                color:#222;
            }
            .text-center{
                text-align: center;
            }

            @media (max-width: 630px) {
                .ops-card-img{
                    display: none;
                }
            }
        `}</style>
    </div>
    )
}

export default OpenSourceProjects
